/* App */

.data-visualizer-app {
    height: 100vh;
    background-color: #f4f6f8;
    overflow: hidden;
}
.data-visualizer-app * {
    outline: none;
}

body {
    font-family: 'Roboto', sans-serif;
}

/* Flex */

.flex-ct {
    display: -ms-flexbox;
    display: flex;
}

.flex-dir-col {
    -ms-flex-direction: column;
        flex-direction: column;
}

.flex-grow-1 {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.flex-basis-0 {
    -ms-flex-preferred-size: 0%;
        flex-basis: 0%;
}

/* Headerbar */

.section-headerbar {
    height: 48px;
}

/* Toolbar */

.section-toolbar {
    height: 39px;
}

.toolbar-type {
    width: 260px;
    border-right: 1px solid #e0e2e4;
    border-bottom: 1px solid #e0e2e4;
}

.toolbar-menubar {
    background-color: #ddd;
}

/* Main */

.main-left {
    min-width: 260px;
    border-right: 1px solid #e0e2e4;
}

.main-right {
    -ms-flex: 1 1;
        flex: 1 1;
    max-width: 380px;
    background-color: #f0f2f4;
    overflow-y: auto;
    overflow-x: hidden;
}

/* Main center */

.main-center-titlebar {
    height: 34px;
}

.main-center-canvas {
    overflow: hidden;
}



